import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { ConfigProvider, Spin } from 'antd';
import { lazy, Suspense } from 'react';
import MainLayout from './layouts/MainLayout';
import { AppProvider } from './contexts/AppContext';
import './App.css';

// Lazy load pages for better code splitting
const AIAgentPage = lazy(() => import('./pages/AIAgentPage'));
const InfrastructureStatePage = lazy(() => import('./pages/InfrastructureStatePage'));
const DependencyGraphPage = lazy(() => import('./pages/DependencyGraphPage'));
const ConflictsPage = lazy(() => import('./pages/ConflictsPage'));
const DeploymentPlanPage = lazy(() => import('./pages/DeploymentPlanPage'));

// Ant Design theme configuration - Based on original web UI
const theme = {
  token: {
    colorPrimary: '#007bff',        // Blue primary color from original
    colorSuccess: '#28a745',        // Green success color
    colorWarning: '#ffc107',        // Amber warning color  
    colorError: '#dc3545',          // Red error color
    colorInfo: '#17a2b8',          // Teal info color
    colorBgContainer: '#ffffff',
    colorBgBase: '#f8f9fa',        // Light gray background
    borderRadius: 6,
    fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
  },
  components: {
    Button: {
      borderRadius: 5,
      fontWeight: 500,
    },
    Card: {
      borderRadius: 8,
      paddingLG: 20,
    },
    Timeline: {
      tailColor: '#dee2e6',
      dotBorderWidth: 2,
    },
  },
};

function App() {
  return (
    <ConfigProvider theme={theme}>
      <AppProvider>
        <Router>
          <div className="App">
            <MainLayout>
              <Suspense fallback={
                <div style={{ 
                  display: 'flex', 
                  justifyContent: 'center', 
                  alignItems: 'center', 
                  minHeight: '400px' 
                }}>
                  <Spin size="large" tip="Loading..." />
                </div>
              }>
                <Routes>
                  <Route path="/" element={<AIAgentPage />} />
                  <Route path="/agent" element={<AIAgentPage />} />
                  <Route path="/state" element={<InfrastructureStatePage />} />
                  <Route path="/graph" element={<DependencyGraphPage />} />
                  <Route path="/conflicts" element={<ConflictsPage />} />
                  <Route path="/plan" element={<DeploymentPlanPage />} />
                </Routes>
              </Suspense>
            </MainLayout>
          </div>
        </Router>
      </AppProvider>
    </ConfigProvider>
  );
}

export default App;
