import { createContext, useContext, useReducer, useEffect } from 'react';

// Initial state
const initialState = {
  // Connection status
  connected: false,
  connectionStatus: 'Disconnected',
  
  // Loading states
  loading: {
    agent: false,
    state: false,
    graph: false,
    conflicts: false,
    plan: false,
  },
  
  // Status message
  statusMessage: 'Ready',
  
  // Current data
  infrastructureState: null,
  dependencyGraph: null,
  conflicts: null,
  deploymentPlan: null,
  
  // Agent execution
  currentExecution: null,
  
  // Agent response persistence
  agentResponse: null,
  agentExecutionPlan: null,
  agentExecutionLogs: [],
  agentExecutionProgress: 0,
  agentExecutionElapsed: '00:00',
  agentExecutionCompleted: false,
  
  // Recovery options
  recoveryOptions: {},
  
  // WebSocket
  websocket: null,
};

// Action types
export const ACTION_TYPES = {
  SET_CONNECTION_STATUS: 'SET_CONNECTION_STATUS',
  SET_LOADING: 'SET_LOADING',
  SET_STATUS_MESSAGE: 'SET_STATUS_MESSAGE',
  SET_INFRASTRUCTURE_STATE: 'SET_INFRASTRUCTURE_STATE',
  SET_DEPENDENCY_GRAPH: 'SET_DEPENDENCY_GRAPH',
  SET_CONFLICTS: 'SET_CONFLICTS',
  SET_DEPLOYMENT_PLAN: 'SET_DEPLOYMENT_PLAN',
  SET_CURRENT_EXECUTION: 'SET_CURRENT_EXECUTION',
  UPDATE_EXECUTION_STATUS: 'UPDATE_EXECUTION_STATUS',
  SET_WEBSOCKET: 'SET_WEBSOCKET',
  REFRESH_INFRASTRUCTURE: 'REFRESH_INFRASTRUCTURE',
  
  // Agent response actions
  SET_AGENT_RESPONSE: 'SET_AGENT_RESPONSE',
  SET_AGENT_EXECUTION_PLAN: 'SET_AGENT_EXECUTION_PLAN',
  UPDATE_STEP_STATUS: 'UPDATE_STEP_STATUS',
  SET_AGENT_EXECUTION_LOGS: 'SET_AGENT_EXECUTION_LOGS',
  ADD_AGENT_EXECUTION_LOG: 'ADD_AGENT_EXECUTION_LOG',
  SET_AGENT_EXECUTION_PROGRESS: 'SET_AGENT_EXECUTION_PROGRESS',
  UPDATE_EXECUTION_PROGRESS: 'UPDATE_EXECUTION_PROGRESS',
  SET_AGENT_EXECUTION_ELAPSED: 'SET_AGENT_EXECUTION_ELAPSED',
  SET_AGENT_EXECUTION_COMPLETED: 'SET_AGENT_EXECUTION_COMPLETED',
  CLEAR_AGENT_RESPONSE: 'CLEAR_AGENT_RESPONSE',
  
  // Recovery actions
  SET_RECOVERY_OPTIONS: 'SET_RECOVERY_OPTIONS',
  CLEAR_RECOVERY_OPTIONS: 'CLEAR_RECOVERY_OPTIONS',
  UPDATE_RECOVERY_PROGRESS: 'UPDATE_RECOVERY_PROGRESS',
};

// Reducer
const appReducer = (state, action) => {
  switch (action.type) {
    case ACTION_TYPES.SET_CONNECTION_STATUS:
      return {
        ...state,
        connected: action.payload.connected,
        connectionStatus: action.payload.status,
      };
    
    case ACTION_TYPES.SET_LOADING:
      return {
        ...state,
        loading: {
          ...state.loading,
          [action.payload.key]: action.payload.value,
        },
      };
    
    case ACTION_TYPES.SET_STATUS_MESSAGE:
      return {
        ...state,
        statusMessage: action.payload,
      };
    
    case ACTION_TYPES.SET_INFRASTRUCTURE_STATE:
      return {
        ...state,
        infrastructureState: action.payload,
      };
    
    case ACTION_TYPES.SET_DEPENDENCY_GRAPH:
      return {
        ...state,
        dependencyGraph: action.payload,
      };
    
    case ACTION_TYPES.SET_CONFLICTS:
      return {
        ...state,
        conflicts: action.payload,
      };
    
    case ACTION_TYPES.SET_DEPLOYMENT_PLAN:
      return {
        ...state,
        deploymentPlan: action.payload,
      };
    
    case ACTION_TYPES.SET_CURRENT_EXECUTION:
      return {
        ...state,
        currentExecution: action.payload,
      };
    
    case ACTION_TYPES.UPDATE_EXECUTION_STATUS:
      return {
        ...state,
        currentExecution: state.currentExecution ? {
          ...state.currentExecution,
          status: action.payload,
        } : null,
      };
    
    case ACTION_TYPES.SET_WEBSOCKET:
      return {
        ...state,
        websocket: action.payload,
      };
    
    case ACTION_TYPES.REFRESH_INFRASTRUCTURE:
      // This is just a trigger to refresh - actual refreshing is handled by the infrastructure page
      return {
        ...state,
        statusMessage: 'Infrastructure state refreshed',
      };
    
    // Agent response actions
    case ACTION_TYPES.SET_AGENT_RESPONSE:
      return {
        ...state,
        agentResponse: action.payload,
      };
    
    case ACTION_TYPES.SET_AGENT_EXECUTION_PLAN:
      return {
        ...state,
        agentExecutionPlan: action.payload,
      };
    
    case ACTION_TYPES.UPDATE_STEP_STATUS:
      if (!state.agentExecutionPlan) {
        return state;
      }
      
      const updatedPlan = state.agentExecutionPlan.map((step) => {
        if (step.id === action.payload.stepId) {
          return { ...step, status: action.payload.status };
        }
        return step;
      });
      
      return {
        ...state,
        agentExecutionPlan: updatedPlan,
      };
    
    case ACTION_TYPES.SET_AGENT_EXECUTION_LOGS:
      return {
        ...state,
        agentExecutionLogs: action.payload,
      };
    
    case ACTION_TYPES.ADD_AGENT_EXECUTION_LOG:
      return {
        ...state,
        agentExecutionLogs: [...(state.agentExecutionLogs || []), action.payload],
      };
    
    case ACTION_TYPES.SET_AGENT_EXECUTION_PROGRESS:
      return {
        ...state,
        agentExecutionProgress: action.payload,
      };
    
    case ACTION_TYPES.UPDATE_EXECUTION_PROGRESS:
      if (!state.agentExecutionPlan) {
        return state;
      }
      
      const completedSteps = state.agentExecutionPlan.filter(step => step.status === 'completed').length;
      const progress = Math.round((completedSteps / state.agentExecutionPlan.length) * 100);
      
      return {
        ...state,
        agentExecutionProgress: progress,
      };
    
    case ACTION_TYPES.SET_AGENT_EXECUTION_ELAPSED:
      return {
        ...state,
        agentExecutionElapsed: action.payload,
      };
    
    case ACTION_TYPES.SET_AGENT_EXECUTION_COMPLETED:
      return {
        ...state,
        agentExecutionCompleted: action.payload,
      };
    
    case ACTION_TYPES.CLEAR_AGENT_RESPONSE:
      return {
        ...state,
        agentResponse: null,
        agentExecutionPlan: null,
        agentExecutionLogs: [],
        agentExecutionProgress: 0,
        agentExecutionElapsed: '00:00',
        agentExecutionCompleted: false,
        currentExecution: null,
        recoveryOptions: {},
      };

    // Recovery actions
    case ACTION_TYPES.SET_RECOVERY_OPTIONS:
      return {
        ...state,
        recoveryOptions: {
          ...state.recoveryOptions,
          [action.payload.stepId]: action.payload,
        },
      };
    
    case ACTION_TYPES.CLEAR_RECOVERY_OPTIONS:
      if (action.payload?.stepId) {
        const { [action.payload.stepId]: removed, ...remainingOptions } = state.recoveryOptions;
        return {
          ...state,
          recoveryOptions: remainingOptions,
        };
      } else {
        return {
          ...state,
          recoveryOptions: {},
        };
      }
    
    case ACTION_TYPES.UPDATE_RECOVERY_PROGRESS:
      if (!state.recoveryOptions[action.payload.stepId]) {
        return state;
      }
      
      return {
        ...state,
        recoveryOptions: {
          ...state.recoveryOptions,
          [action.payload.stepId]: {
            ...state.recoveryOptions[action.payload.stepId],
            progressMessage: action.payload.message,
          },
        },
      };

    default:
      return state;
  }
};

// Create context
const AppContext = createContext();

// Provider component
export const AppProvider = ({ children }) => {
  const [state, dispatch] = useReducer(appReducer, initialState);

  // WebSocket setup - just initialize and store, don't handle messages
  useEffect(() => {
    const initializeWebSocket = () => {
      // Use environment variable for WebSocket URL, fallback to auto-detect
      let wsUrl = process.env.REACT_APP_WS_URL;
      
      if (!wsUrl) {
        // Auto-detect WebSocket URL based on current location
        const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
        wsUrl = `${protocol}//${window.location.host}/ws`;
      }
      
      console.log('Connecting to WebSocket:', wsUrl);
      
      try {
        const ws = new WebSocket(wsUrl);
        
        ws.onopen = () => {
          console.log('WebSocket connection established');
          dispatch({
            type: ACTION_TYPES.SET_CONNECTION_STATUS,
            payload: { connected: true, status: 'Connected' },
          });
        };
        
        ws.onclose = (event) => {
          console.log('WebSocket connection closed:', event.code, event.reason);
          dispatch({
            type: ACTION_TYPES.SET_CONNECTION_STATUS,
            payload: { connected: false, status: 'Disconnected' },
          });
          
          // Attempt to reconnect after 5 seconds for abnormal closes
          if (event.code !== 1000) {
            setTimeout(initializeWebSocket, 5000);
          }
        };
        
        ws.onerror = (error) => {
          console.error('WebSocket error:', error);
          dispatch({
            type: ACTION_TYPES.SET_CONNECTION_STATUS,
            payload: { connected: false, status: 'Connection Error' },
          });
        };

        // Store the WebSocket instance in context for components to use
        dispatch({ type: ACTION_TYPES.SET_WEBSOCKET, payload: ws });
        
        // Cleanup on unmount
        return () => {
          if (ws.readyState === WebSocket.OPEN) {
            ws.close(1000, 'Component unmounting');
          }
        };
      } catch (error) {
        console.error('WebSocket connection failed:', error);
        dispatch({
          type: ACTION_TYPES.SET_CONNECTION_STATUS,
          payload: { connected: false, status: 'WebSocket Unavailable' },
        });
      }
    };

    const cleanup = initializeWebSocket();
    
    return cleanup;
  }, []);

  const value = {
    state,
    dispatch,
  };

  return <AppContext.Provider value={value}>{children}</AppContext.Provider>;
};

// Hook to use the context
export const useApp = () => {
  const context = useContext(AppContext);
  if (context === undefined) {
    throw new Error('useApp must be used within an AppProvider');
  }
  return context;
};

export default AppContext;