import React, { useState, useEffect } from 'react';
import { Layout, Menu, Typography, Space, Button } from 'antd';
import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  RobotOutlined,
  DatabaseOutlined,
  NodeIndexOutlined,
  ExclamationCircleOutlined,
  CalendarOutlined,
  CheckCircleOutlined,
  CloseCircleOutlined,
  WarningOutlined,
} from '@ant-design/icons';
import { useNavigate, useLocation } from 'react-router-dom';
import { useApp } from '../contexts/AppContext';

const { Header, Sider, Content, Footer } = Layout;
const { Title, Text } = Typography;

const MainLayout = ({ children }) => {
  const [collapsed, setCollapsed] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();
  const { state } = useApp();

  // Handle responsive breakpoints
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  
  useEffect(() => {
    const handleResize = () => {
      const mobile = window.innerWidth <= 768;
      setIsMobile(mobile);
      if (mobile) {
        setCollapsed(true);
      }
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Menu items configuration
  const menuItems = [
    {
      key: '/agent',
      icon: <RobotOutlined />,
      label: 'AI Agent',
    },
    {
      key: '/state',
      icon: <DatabaseOutlined />,
      label: 'Infrastructure State',
    },
    {
      key: '/graph',
      icon: <NodeIndexOutlined />,
      label: 'Dependency Graph',
    },
    {
      key: '/conflicts',
      icon: <ExclamationCircleOutlined />,
      label: 'Conflicts',
    },
    {
      key: '/plan',
      icon: <CalendarOutlined />,
      label: 'Deployment Plan',
    },
  ];

  const toggleSider = () => {
    setCollapsed(!collapsed);
  };

  const handleMenuClick = ({ key }) => {
    navigate(key);
    // Auto-collapse on mobile after navigation
    if (isMobile) {
      setCollapsed(true);
    }
  };

  const getConnectionIcon = () => {
    if (state.connected) {
      return <CheckCircleOutlined style={{ color: '#52c41a' }} />;
    } else if (state.connectionStatus === 'Connection Error') {
      return <WarningOutlined style={{ color: '#faad14' }} />;
    } else {
      return <CloseCircleOutlined style={{ color: '#ff4d4f' }} />;
    }
  };

  const getCurrentDateTime = () => {
    return new Date().toLocaleString();
  };

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Sider 
        trigger={null} 
        collapsible 
        collapsed={collapsed}
        collapsedWidth={isMobile ? 0 : 80}
        style={{
          background: '#001529',
          position: 'fixed',
          left: 0,
          top: 0,
          bottom: 0,
          zIndex: 1000,
          height: '100vh',
          overflow: 'auto',
        }}
        width={250}
        breakpoint="lg"
        onBreakpoint={(broken) => {
          setCollapsed(broken);
        }}
      >
        <div
          style={{
            height: 64,
            display: 'flex',
            alignItems: 'center',
            justifyContent: collapsed ? 'center' : 'flex-start',
            padding: collapsed ? '0' : '0 16px',
            background: '#002140',
          }}
        >
          <img 
            src="/ai-infrastructure-agent.svg" 
            alt="AI Agent"
            style={{ 
              height: '32px', 
              width: 'auto', 
              filter: 'brightness(0) invert(1)',
              marginRight: collapsed ? 0 : 8
            }}
          />
          {!collapsed && (
            <span style={{ color: 'white', fontSize: '16px', fontWeight: 500 }}>
              AI Agent
            </span>
          )}
        </div>
        
        <Menu
          theme="dark"
          mode="inline"
          selectedKeys={[location.pathname]}
          items={menuItems}
          onClick={handleMenuClick}
          style={{ borderRight: 0 }}
        />
      </Sider>
      
      <Layout style={{ marginLeft: collapsed ? (isMobile ? 0 : 80) : 250, transition: 'margin-left 0.2s' }}>
        <Header
          style={{
            padding: '0 16px',
            background: '#fff',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            boxShadow: '0 2px 8px rgba(0, 0, 0, 0.1)',
            position: 'fixed',
            top: 0,
            right: 0,
            left: collapsed ? (isMobile ? 0 : 80) : 250,
            zIndex: 999,
            transition: 'left 0.2s',
          }}
        >
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <Button
              type="text"
              icon={collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
              onClick={toggleSider}
              style={{
                fontSize: '16px',
                width: 64,
                height: 64,
              }}
            />
            
            <Title level={3} style={{ margin: 0, marginLeft: 16 }}>
              AI Infrastructure Agent
            </Title>
          </div>

          <Space size="large">
            <div>
              <Text type="secondary" style={{ fontSize: '12px' }}>
                {getCurrentDateTime()}
              </Text>
            </div>
            
            <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
              {getConnectionIcon()}
              <Text strong={state.connected}>
                {state.connectionStatus}
              </Text>
            </div>
          </Space>
        </Header>
        
        <Content
          style={{
            padding: '24px',
            background: '#f0f2f5',
            minHeight: 'calc(100vh - 128px)', // Account for header and footer
            marginTop: 64, // Account for fixed header height
            marginBottom: 55,
            overflow: 'auto',
          }}
        >
          {children}
        </Content>
        
        <Footer
          style={{
            padding: '16px 24px',
            background: '#fff',
            borderTop: '1px solid #f0f0f0',
            textAlign: 'center',
            position: 'fixed',
            bottom: 0,
            right: 0,
            left: collapsed ? (isMobile ? 0 : 80) : 250,
            zIndex: 999,
            transition: 'left 0.2s',
          }}
        >
            Copyright @ 2025 by DevOps VN Team. All rights reserved.
        </Footer>
      </Layout>
    </Layout>
  );
};

export default MainLayout;